import React from 'react';
import Select from 'react-select';
import FlowComponent from '../../common/component/core/FlowComponent';
import {submitCheckin} from '../../../actions/field_visit_actions';
import {getCustProfile} from '../../../actions/borrower_actions';
import {checkLastVisit} from '../../../actions/field_visit_actions';
import {get} from '../../../helpers/storage_helper';
import {  Redirect } from 'react-router-dom';
import {format_date} from '../../../helpers/common_helper';
import moment from 'moment';
class CheckIn extends FlowComponent{
    
    state = {
              server_time : {}, 
               checkin_req : {},
               toSuccess : false,
               select_value : []
               
       }

       
     
componentWillMount(){
    this.server_time()
    this.cust_id = this.props.match.params.cust_id
    getCustProfile(this.req({cust_id : this.cust_id, incl_addr : true}, true)).
        then((response) =>{
             if(!response){return };
             if(response.status === "success"){
                 if(response.data){
                     this.setState({...response.data, cust_id : this.cust_id})
                 }
                
             }
             else{
               alert(response.message  +"\n" + JSON.stringify(response.data));  
             }
        } )
  }

  
  validate_search(checkin_req) {
     if(checkin_req.remarks && checkin_req.visit_purpose ){
      return true
    }else{

      alert("Please enter required fields")

      return false}
    
  }

  handleSubmit = (event) =>{
       var checkin_req = this.state.checkin_req;
       var is_valid = this.validate_search(checkin_req)
       if(is_valid){
         checkin_req.cust_id = this.state.cust_id;
         checkin_req.visit_start_time = this.state.server_time.server_time_db;
         checkin_req.time_spent = 0;
         submitCheckin(this.req({checkin_req}))
             .then((response) => {
                     if(!response){return };
                 if(response.status === "success"){
                     alert(response.message)
                     this.setState({toSuccess : true})
                 }
                 else{
                   alert(response.message  +"\n" + JSON.stringify(response.data));  
                 }
            })
         }    
  }  
  handleCheckinChange = (event) =>{
      var checkin_req = this.state.checkin_req
      checkin_req[event.target.id] = event.target.value
      this.setState({checkin_req});
  }
  
  handlePurposeChange = (selectedOptions) =>{

       const select_value = selectedOptions
       selectedOptions = selectedOptions.map((item) => ( item.value));
       if( (selectedOptions.includes('overdue_visit')) && (this.state.is_og_loan_overdue !== 1) )
       {
          alert("Customer do not have an overdue FA");
       }else{
           var checkin_req = this.state.checkin_req
           checkin_req.visit_purpose = selectedOptions
           this.setState({checkin_req, select_value});
       }
  }     
       

render(){
    if (this.state.toSuccess === true) {
      
      return <Redirect to={`/borrower/indiv/view/${this.state.cust_id}`}/> 
    }

    return(
        <div className="container">
        <br/>
        <h4>Customer Visit for <b>{this.state.cust_name} | {this.state.biz_name} </b></h4>
        <br/>
       <form>
            <div className="rule-form-group  form-group row reset-margin">
                   <label for="" className="col-sm-5 mb-3 label__name font-weight-bold reset-padding">Flow Cust ID | DP Cust ID</label>
               <div className="col-sm-7 reset-padding">
                   <div>
                        <label className="" id = "cust_id" >{this.state.cust_id} | {this.state.data_prvdr_cust_id}</label>
                   </div>
                   <div className="control-me"></div>
               </div>
            </div>
             <div className="rule-form-group  form-group row reset-margin">
                   <label for="" className="col-sm-5 mb-3 label__name font-weight-bold reset-padding">Customer Name</label>
               <div className="col-sm-7 reset-padding">
                   <div>
                        <label className="" id = "cust_id" >{this.state.cust_name}</label>
                   </div>
                   <div className="control-me"></div>
               </div>
            </div>
            <div className="rule-form-group form-group row reset-margin">
                   <label for="" className="col-sm-5 mb-3 font-weight-bold  label__name reset-padding">Address</label>
               <div className="col-sm-7 reset-padding">
                   <div>
            <label className="" id = "cust_id">{this.state.cust_addr_txt}</label>
                   </div>
                   <div className="control-me"></div>
               </div>
            
            </div>
            <div className="rule-form-group form-group row reset-margin">
                   <label for="" className="col-sm-5 mb-3 font-weight-bold  label__name reset-padding">Check In At</label>
               <div className="col-sm-7 reset-padding">
                   <div>
                           <b><label style={{color:'blue'}} className="" id = "visit_start_time">{this.state.server_time.server_time_ui}</label>
                             </b>      
                   </div>
                   <div className="control-me"></div>
               </div>
            
            </div>
           
            <div className="rule-form-group form-group row mx-0 mb-3  ">
                   <label for=""  className="col-sm-5 font-weight-bold label__name reset-padding">Visit Purpose<font className="text-danger">   *  </font></label>
               <div className="col-sm-7 reset-padding">
                  
                 

                      <Select onChange={(e) =>{this.handlePurposeChange(e);}}
                              value = {this.state.select_value}
                              options={this.getMultiSelectOptions('visit_purpose')} isMulti/>

                   <div className="control-me"></div>
               </div>
            
            </div>
            <div className="rule-form-group form-group row reset-margin ">
                   <label for="" className="col-sm-5 font-weight-bold label__name reset-padding">Remarks<font className="text-danger">   *  </font></label>
               <div className="col-sm-7 reset-padding">
                   <div>
                   <textarea type="text" id="remarks" className="form-control" onChange={(e) =>{this.handleCheckinChange(e);}}  maxLength='256'/>
            </div>
            </div>

            </div>
            {/*<div className="rule-form-group form-group row reset-margin">
            <label class="cameraButton">Take a picture</label>
               <div className="col-sm-7 reset-padding">
                   <div>                     
                           <input type="file" accept="image/*" capture=""/>
                                            
                   </div>
                   <div className="control-me"></div>
               </div>
            
            </div>*/}
        </form>
        <br/>
        <button type="button" className= "btn btn-primary float-right mr-1" onClick={this.handleSubmit} disable>Submit</button>
        </div>
        
        )
}

}

export default CheckIn;